@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  font-family: 'Lato', sans-serif;
}

.filter-teal {
  filter: invert(77%) sepia(39%) saturate(681%) hue-rotate(110deg)
    brightness(88%) contrast(85%);
}
.teal-color {
  color: #3fceaf;
}
.grey-color {
  color: #1f1f1f;
}
.teal-background {
  background-color: #3fceaf;
}
.grey-background {
  background-color: #1f1f1f;
}
.header-logo{
  margin: 10px;
  margin-left: 15%;
  max-height: 20px;
}

@media screen and (max-width: 768px) {
  .header-container{
    text-align: center;
  }

  .header-logo{
    margin: 10px;
    max-height: 30px;
  }
}

@media screen and (max-width: 480px) {
  .header-container{
    text-align: center;
  }
  
  .header-logo{
    margin: 10px;
    max-height: 30px;
  }
}
.home-page-body {
  position: relative;
}

.body-background {
  width: 100%;
  z-index: -1;
}

.body-text {
  color: white;
  position: absolute;
  top: calc( 1.55vw * 12);
  left: 10%;
}

.body-text-header-1 {
  font-size: 19px;
  font-weight: 600;
}

.body-text-header-2 {
  font-size: 32px;
  font-weight: 400;
}

.body-text-50 {
  max-width: 50%;
  font-size: 16px;
  font-weight: 600;
  margin: 10px 0px 25px 0px;
}

.how-it-works-container {
  text-align: center;
  position: relative;
  top: 0px;
  max-width: 1200px;
  margin: auto;
}

.how-it-works-grid {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 30px;
}

.how-it-works-item-header {
  font-size: 48px;
  font-weight: 900;
  margin-bottom: 5px;
}

.how-it-works-item-divider {
  width: 60px;
  background-color: #5cf7d6;
  border: none;
  height: 4px;
  margin-top: 2px;
}

.how-it-works-item-title {
  font-weight: 900;
}

@media screen and (min-width: 1200px) {
  .body-text {
    top: calc( 1.55vw * 18);
  }

  .how-it-works-header {
    font-size: 48px;
    margin: 20px 0px 30px 0px;
  }

  .body-text-header-1 {
    font-size: 32px;
    font-weight: 600;
  }
  
  .body-text-header-2 {
    font-size: 56px;
    font-weight: 400;
  }

  .body-text-50 {
    max-width: 50%;
    font-size: 24px;
    font-weight: 600;
    margin: 10px 0px 25px 0px;
  }
} 

@media screen and (max-width: 1200px) {
  .how-it-works-container {
    margin: 0px 30px;
  }
}

@media screen and (max-width: 768px) {
  .body-text {
    top: calc( 1.55vw * 5);
  }

  .body-text-50 {
    max-width: 75%
  }

  .how-it-works-header {
    font-size: 20px;
  }

  .how-it-works-grid {
    grid-template-columns: auto auto;
    grid-gap: 30px;
  }
}

@media screen and (max-width: 480px) {
  .how-it-works-grid {
    display: grid;;
    grid-template-columns: auto;
    grid-gap: 30px;
  }

  .body-text {
    position: static;
    margin: 10px 5px;
    color: #000;
    text-align: center;

  }

  .body-text-50 {
    max-width: 100%;
    
    font-weight: 400;
  }
}



.btn-link {
  text-decoration: none;
}

.btn-get-started {
  overflow: hidden;
}

.btn-get-started:after {
  left: -100%;
  -webkit-clip-path: polygon(10% 0, 70% 0, 90% 100%, 30% 100%);
          clip-path: polygon(10% 0, 70% 0, 90% 100%, 30% 100%);
  background-color: rgba(255, 255, 255, 0.6);
  transition: all 300ms ease;
}

.btn-get-started:hover::after {
  left: 100%;
}

.btn {
  background-color: transparent;
  color: #FFF;
  background-color: #3fceaf;
  padding: 10px 30px;
  border-radius: 25px;
  border: none;
  overflow: hidden;
  box-shadow: none;

  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.8s ease;
  position: relative;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.btn::before,
.btn::after {
  position: absolute;
  display: block;
  content: "";
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 480px) {
  .btn {
    margin: auto;
    margin-bottom: 30px;
  }
}
.footer {
  height: 60px;
  background-color: #1f1f1f;
  width: 100%;
  margin-top: 30px;
  align-items: center;
  justify-content: end;
  display: flex;
}

.footer-text {
  color: #3fceaf;
  float: right;
  margin-right: 20px;
  font-weight: 800;
}

@media screen and (max-width: 768px) {
  .footer{
    justify-content: center;
  }
}
